import React, { useState } from "react";
import CorrectionApplication from "../leave_management/attendanceCorrectionApplication/CorrectionApplication";
import AttendanceLogs from "../leave_management/attendanceLogsApplication/AttendanceLogs";
import { isAfterMoment, isBeforeMoment } from "../../utils/common";
import { PunchNotAvailable } from "./attendanceDashboard";
import AttendanceLogsModal from "./AttendanceLogsModal";
import LeaveApplication from "../leave_management/leaveApplication/LeaveApplication";
import TourApplication from "../leave_management/tourApplication/TourApplication";
import { CustomError } from "../commanComponent";
const SelectLeaveTypeModal = (props) => {
  const { selectedCalenderData, isOpen, handleOpenClose } = props
  const [isShowCorrectionModel, setIsShowCorrectionMode] = useState()
  const [isShowAllBreak, setIsShowAllBreak] = useState(false)
  const [viewModalType, setViewModalType] = useState('')
  const [updatedLogsData, setUpdatedLogsData] = useState([])
  const [isShowAttendanceLogs, setIsShowAttendanceLogs] = useState(false)
  const [isOpenLeave, setIsOpenLeave] = useState(false)
  const [isOpenTourModal, setIsOpenTourModal] = useState(false)

  /* Apply Attendance Correction  */
  const toggleCorrectionApplicationModal = () => {
    setIsShowCorrectionMode(!isShowCorrectionModel)
  }
  const toggleShowAllBreakModal = (type) => {
    if (type === "showLogs") {
      setIsShowAllBreak(!isShowAllBreak)
      setViewModalType(type)
    } else {
      setIsShowAllBreak(!isShowAllBreak)
      setIsShowCorrectionMode(!isShowCorrectionModel)
      setViewModalType('')
    }
  }
  const toggleAttendanceLogs = () => {
    setIsShowAttendanceLogs(!isShowAttendanceLogs)
  }
  const toggleLeaveApplyModal = () => {
    setIsOpenLeave(!isOpenLeave)
  }

  const toggleTourApplicationModel = () => {
    setIsOpenTourModal(!isOpenTourModal)
  }
  const renderedLeaveCards = () => {
    const cards = [];
    const isNull=selectedCalenderData?.attendance_status===null?true:false
    const isWO = selectedCalenderData?.attendance_status === "WO";
    const isPR = selectedCalenderData?.attendance_status === "PR";
    const isBefore = isBeforeMoment(selectedCalenderData?.attendance_date);
    const isAfter = isAfterMoment(selectedCalenderData?.attendance_date);
  
    // Apply Leave (only if not WO)
    if (!isWO && !isPR ) {
      cards.push(
        <div key="apply-leave" className="col-md-6 px-md-2 mb-3" onClick={toggleLeaveApplyModal}>
          <span className="totalBx h-100">
            <figure>
              <img src="images/totalleaves-icon.svg" alt="" width={50} />
            </figure>
            <h3 className="fw-bolder my-0">Apply Leave</h3>
            <img src="images/totalattendance.svg" className="cornerLine" alt="" width={95} />
          </span>
        </div>
      );
    }
  
    // Apply Tour (only if not WO)
    if ((isWO && !isPR)||isNull  ) {
      cards.push(
        <div key="apply-tour" className="col-md-6 px-md-2 mb-3" onClick={toggleTourApplicationModel}>
          <span className="totalBx h-100">
            <figure>
              <img src="images/apply-tour.svg" alt="" width={50} />
            </figure>
            <h3 className="fw-bolder my-0">Apply Tour</h3>
            <img src="images/totalattendance.svg" className="cornerLine" alt="" width={80} />
          </span>
        </div>
      );
    }
  
    // Attendance Correction (if not PR/WO and date is before today)
    if (isBefore && !isPR && !isWO) {
      cards.push(
        <div key="attendance-correction" className="col-md-6 px-md-2 mb-3" onClick={toggleCorrectionApplicationModal}>
          <span className="totalBx h-100">
            <figure>
              <img src="images/attendance-correction.svg" alt="" width={50} />
            </figure>
            <h3 className="fw-bolder my-0">Attendance Correction</h3>
            <img src="images/totalattendance.svg" className="cornerLine" alt="" width={80} />
          </span>
        </div>
      );
    }
  
    // Always show Attendance Logs
    if (!isWO && !isAfter){
      cards.push(
        <div key="attendance-logs" className="col-md-6 px-md-2 mb-3" onClick={toggleAttendanceLogs}>
          <span className="totalBx h-100">
            <figure>
              <img src="images/attendance-logs.svg" alt="" width={50} />
            </figure>
            <h3 className="fw-bolder my-0">Attendance Logs</h3>
            <img src="images/totalattendance.svg" className="cornerLine" alt="" width={80} />
          </span>
        </div>
      );
    }
    return cards.length>0?<div className="row px-md-1">{cards}</div>:<CustomError text ={"No Leave Type Available"} />
  };
  
  return (
    <React.Fragment>
      <div className="modal fade selectModal" id="selectModal" style={{ display: isOpen ? "unset !important" : "none !important", opacity: isOpen ? "unset" : "0" }}>
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content rounded-0 border-0">
            <div className="modal-header border-0 pb-0">
              <h1 className="modal-title fs-5 fw-bold text-primary">Select</h1>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
                onClick={() => handleOpenClose()}
              />
            </div>
            <div className="modal-body">
            {renderedLeaveCards()}
              {/* <div className="row px-md-1">
                {selectedCalenderData?.attendance_status !== "WO" && <div className="col-md-6 px-md-2 mb-3" onClick={() => toggleLeaveApplyModal()}>
                  <span

                    className="totalBx h-100"
                  >
                    <figure>
                      <img src="images/totalleaves-icon.svg" alt="" width={50} />
                    </figure>
                    <h3 className="fw-bolder my-0">Apply Leave</h3>
                    <img
                      src="images/totalattendance.svg"
                      className="cornerLine"
                      alt=""
                      width={95}
                    />
                  </span>
                </div>}
                {selectedCalenderData?.attendance_status !== "WO" && <div className="col-md-6 px-md-2 mb-3">
                  <span
                    className="totalBx h-100"
                    onClick={() => toggleTourApplicationModel()}
                  >
                    <figure>
                      <img src="images/apply-tour.svg" alt="" width={50} />
                    </figure>
                    <h3 className="fw-bolder my-0">Apply Tour</h3>
                    <img
                      src="images/totalattendance.svg"
                      className="cornerLine"
                      alt=""
                      width={80}
                    />
                  </span>
                </div>}
                {
                  isBeforeMoment(selectedCalenderData?.attendance_date)
                  && selectedCalenderData?.attendance_status !== "PR" &&
                  selectedCalenderData?.attendance_status !== "WO" && <div className="col-md-6 px-md-2 mb-3"
                    onClick={() => toggleCorrectionApplicationModal()}
                  >
                    <span
                      // data-bs-toggle="offcanvas"
                      className="totalBx h-100"
                    >
                      <figure>
                        <img
                          src="images/attendance-correction.svg"
                          alt=""
                          width={50}
                        />
                      </figure>
                      <h3 className="fw-bolder my-0">Attendance Correction</h3>
                      <img
                        src="images/totalattendance.svg"
                        className="cornerLine"
                        alt=""
                        width={80}
                      />
                    </span>
                  </div>
                }
                <div className="col-md-6 px-md-2 mb-3">
                  <span

                    className="totalBx h-100"
                    onClick={() => toggleAttendanceLogs()}
                  >
                    <figure>
                      <img src="images/attendance-logs.svg" alt="" width={50} />
                    </figure>
                    <h3 className="fw-bolder my-0">Attendance Logs</h3>
                    <img
                      src="images/totalattendance.svg"
                      className="cornerLine"
                      alt=""
                      width={80}
                    />
                  </span>
                </div>
              </div> */}
            </div>
          </div>
        </div>
        {selectedCalenderData?.last_check_out_time || selectedCalenderData?.first_check_in_time ?
          isShowCorrectionModel && <CorrectionApplication
            handleOpenClose={toggleCorrectionApplicationModal}
            openModel={isShowCorrectionModel}
            toggleShowAllBreakModal={toggleShowAllBreakModal}
            selectedCalenderData={selectedCalenderData}
            updatedLogsData={updatedLogsData}

          /> :
          <PunchNotAvailable
            handleOpenClose={toggleCorrectionApplicationModal}
            openModel={isShowCorrectionModel}
          />
        }
        {isShowAllBreak && <AttendanceLogs
          handleOpenClose={toggleShowAllBreakModal}
          openModel={isShowAllBreak}
          viewModalType={viewModalType}
          selectedCalenderData={selectedCalenderData}
          setUpdatedLogsData={setUpdatedLogsData}
        />}
        {isShowAttendanceLogs && <AttendanceLogsModal
          isOpen={isShowAttendanceLogs}
          handleOpenClose={toggleAttendanceLogs}
          selectedData={selectedCalenderData}
        />
        }
        {isOpenLeave && <LeaveApplication
          openModel={isOpenLeave}
          openLeaveApplicationModel={toggleLeaveApplyModal}
        />}
        {isOpenTourModal && <TourApplication
          openTourApplicationModel={toggleTourApplicationModel}
          isOpenModal={isOpenTourModal}
        />}
      </div>

      {isOpen && <div className="modal-backdrop fade show"></div>}
    </React.Fragment>
  );
};

export default SelectLeaveTypeModal;
